import React from "react";
import "./About.css";
import css from "../../images/tecnologies/css.png";
import html from "../../images/tecnologies/html.png";
import javascript from "../../images/tecnologies/javascript.png";
import nodejs from "../../images/tecnologies/nodejs.png";
import react from "../../images/tecnologies/react.png";
import typescript from "../../images/tecnologies/typescript.png";

const About = (props) => {
  return (
    <section className="About__container  ">
      <div className="About__description ">
        <p>
          Robinson Castro escolheu o curso de Ciência da Computação como
          graduação. No inicio da carreira de T.I trabalhou com a linguagem PHP
          e cms WordPress. Apesar de amar programar em PHP, investiu na
          linguagem JavaScript pois a considera mais versátil para as demandas
          da web e ainda possibilitava criar aplicativos para celular. Decidiu
          focar em uma única stack e hoje é fã de React, React-Native e NodeJS.
        </p>

        <p>
          Atualmente trabalha como freelancer desenvolvendo sistemas e
          aplicativos. Dá uma olhada no portifólio :)
        </p>
      </div>
      <div className="About__tecnologies">
        <ul>
          <li>
            <img src={html} alt="html" />
          </li>
          <li>
            <img src={css} alt="css" />
          </li>
          <li>
            <img src={javascript} alt="javascript" />
          </li>
          <li>
            <img src={nodejs} alt="nodejs" />
          </li>
          <li>
            <img src={react} alt="react" />
          </li>
          <li>
            <img src={typescript} alt="typescript" />
          </li>
        </ul>
      </div>
    </section>
  );
};

export default About;
