import React, { useState } from "react";
import "./Modal.css";

const Modal = (props) => {
  if (props.show == false) {
    return null;
  }

  console.log(props.imagens);
  return (
    <div className="Modal__modal">
      <span
        className="Modal__close_button"
        onClick={() => props.handleStateChangeInModal(props.show)}
      >
        x
      </span>

      <div className="Modal__modal-content">
        {/* <p>{props.children}</p> */}
        <div className="Modal__images_container">
          {props.imagens.map((item, index) => (
            <div className="image_box">{item}</div>
          ))}
        </div>
      </div>

      <div className="directions ">
        <span
          className={props.minSizeReached ? "hideBtn" : ""}
          onClick={() => props.previousproject(props.id)}
        >
          anterior
        </span>
        <span
          className={props.maxSizeReached ? "hideBtn" : ""}
          onClick={() => props.nextproject(props.id)}
        >
          proximo
        </span>
      </div>
    </div>
  );
};

export default Modal;
