import React, { useState } from "react";
import { CSSTransition } from "react-transition-group";
import Modal from "../Modal/Modal";
import "./Portifolio.css";
import projectsData from "./projectsData";

const Portifolio = (props) => {
  const [show, setShow] = useState(false);
  const [texto, setTexto] = useState("");
  const [img, setImg] = useState([]);
  const [active, setActive] = useState("all");
  const [idProject, setIdProject] = useState(null);
  const [maxSizeReached, setmaxSizeReached] = useState(false);
  const [minSizeReached, setminSizeReached] = useState(false);

  const handleStateChangeInModal = (show) => {
    setShow(!show);
  };

  const duration = 500;

  const defaultStyle = {
    transition: `opacity ${duration}ms ease-out`,
    opacity: 0,
    // backgroundColor: "red",
    // height: "300",
    // width: "300",
  };

  const transitionStyles = {
    entering: { opacity: 1 },
    entered: { opacity: 1 },
    exiting: { opacity: 0 },
    exited: { opacity: 0 },
  };

  let projects = [];

  for (const key in projectsData) {
    projects.push(projectsData[key]);
  }

  const [myProjects, setMyProjects] = useState(
    projects.map((item, index) => {
      return (
        <div
          key={projects[index].id}
          className="container-img "
          onClick={() =>
            showModal(
              projects[index].text,
              projects[index].img_inside,
              projects[index].id
            )
          }
        >
          <img
            className="portifolio_img "
            src={projects[index].img}
            alt="Serious cat"
          />
          <div class="middle">
            <div class="text">{projects[index].text}</div>
          </div>
        </div>
      );
    })
  );

  let imagens = img.map((index) => {
    return <img src={index} alt="" />;
  });

  const handleNextProjectOnClick = (id) => {
    console.log("tamanho", projects.length);
    console.log("id", id);
    console.log("idProject", idProject);

    if (id === projects.length - 1) {
      setImg(projects[id].img_inside);
      console.log("atingiu o tamanho maximo");
      setmaxSizeReached(true);
      setminSizeReached(false);
      console.log("maxSizeReached", maxSizeReached);
    } else if (id < projects.length - 1) {
      setImg(projects[id + 1].img_inside);
      setIdProject(id + 1);
      setminSizeReached(false);
      setmaxSizeReached(false);
      console.log("maxSizeReached", maxSizeReached);
    }

    modal = (
      <Modal
        show={show}
        handleStateChangeInModal={handleStateChangeInModal}
        imagens={imagens}
        nextproject={this}
        previousproject={handlePreviousProjectOnClick}
        id={idProject}
        maxSizeReached={maxSizeReached}
        minSizeReached={minSizeReached}
      >
        {texto}
      </Modal>
    );
  };

  const handlePreviousProjectOnClick = (id) => {
    console.log("tamanho", projects.length);
    console.log("id", id);
    console.log("idProject", idProject);

    if (id === 0) {
      setImg(projects[id].img_inside);
      console.log("atingiu o tamanho minimo");
      setminSizeReached(true);
      setmaxSizeReached(false);
      console.log("minSizeReached", minSizeReached);
    } else {
      setImg(projects[id - 1].img_inside);
      setIdProject(id - 1);
      setminSizeReached(false);
      setmaxSizeReached(false);
      console.log("minSizeReached", minSizeReached);
    }

    modal = (
      <Modal
        show={show}
        handleStateChangeInModal={handleStateChangeInModal}
        imagens={imagens}
        previousproject={this}
        nextproject={handleNextProjectOnClick}
        id={idProject}
        maxSizeReached={maxSizeReached}
        minSizeReached={minSizeReached}
      >
        {texto}
      </Modal>
    );
  };

  let modal = (
    <Modal
      show={show}
      handleStateChangeInModal={handleStateChangeInModal}
      imagens={imagens}
      nextproject={handleNextProjectOnClick}
      previousproject={handlePreviousProjectOnClick}
      id={idProject}
      maxSizeReached={maxSizeReached}
      minSizeReached={minSizeReached}
    >
      {texto}
    </Modal>
  );

  const showModal = (text, arrayImg, id) => {
    setImg(arrayImg);
    setTexto(text);
    setIdProject(id);
    setShow(true);
  };

  const filteredProjects = (typeProject) => {
    setInProp(!inProp);

    let projectsFiltered;
    if (typeProject !== "all") {
      projectsFiltered = projects.filter((item, index) => {
        return projects[index].type === typeProject;
      });
    } else {
      projectsFiltered = projects.filter((item, index) => {
        return true;
      });
    }

    setMyProjects(
      projectsFiltered.map((item, index) => {
        console.log("item", projects[index].text);
        console.log("index", index);
        return (
          <CSSTransition in={inProp} timeout={500} classNames="my-node">
            <div
              key={projectsFiltered[index].id}
              onClick={() =>
                showModal(
                  projectsFiltered[index].text,
                  projectsFiltered[index].img_inside,
                  projectsFiltered[index].id
                )
              }
            >
              <img
                className="portifolio_img rounded-lg"
                src={projectsFiltered[index].img}
                alt="Serious cat"
              />

              <div className="middle">
                <div className="text">{projectsFiltered[index].text}</div>
              </div>
            </div>
          </CSSTransition>
        );
      })
    );
  };

  const [inProp, setInProp] = useState(false);

  return (
    <div className="Portifolio__container ">
      <h1 className="flex justify-center sectionTitle">Portfolio Recente</h1>

      <div className="portifolio__categories my-10 ">
        <ul className="flex justify-center">
          <li
            onClick={() => {
              filteredProjects("all");
              setActive("all");
            }}
            className={active === "all" && "active"}
          >
            Todos
          </li>

          <li
            onClick={() => {
              filteredProjects("web_site");
              setActive("web_site");
            }}
            className={active === "web_site" && "active"}
          >
            Site
          </li>
          <li
            onClick={() => {
              filteredProjects("web_system");
              setActive("web_system");
            }}
            className={active === "web_system" && "active"}
          >
            Sistemas
          </li>
          <li
            onClick={() => {
              filteredProjects("mobile");
              setActive("mobile");
            }}
            className={active === "mobile" && "active"}
          >
            Mobile
          </li>
        </ul>
      </div>

      <section id="photos" className="">
        {myProjects}
      </section>
      {modal}
    </div>
  );
};

export default Portifolio;
